.inputOptions{
    display: flex;
    margin-top: 10px;
    padding: 19px 22px;
    align-items: center;
}

.inputOptions > h4 {
    margin-left: 10px;
    color: gray;
    font-weight: 500;
}

.inputOptions:hover{
    cursor: pointer;
    background-color: #e3e3e3;
    border-radius: 10px;
}